<template>
  <div id="tabs" class='ed_hidden_view ed_tabs_container'>
    <div class="ed_select_container ed_text_start">
      <h3 class="ed_mt_none"></h3>
      <div>
        <p class="ed_mb_none"></p>
        <div class="flex items-center gap-4">
          <div>
            <select id="printer-ID" v-model="selectedPrinterID">
              <option value=""></option>
              <option v-for="printer in printers" :key="printer.id" :value="printer.printer_id">{{ printer.printer_name }}</option>
            </select>
          </div>
          <div :class="'delete_btn' + (selectedPrinterID ? '' : ' hidden_menu')" @click="deletePrinterSettings"></div>
        </div>
      </div>

    </div>

    <ul>
      <li><a href="#edit"></a></li>
      <li><a href="#preview"></a></li>
      <li><a href="#print"></a></li>
      <li><a href="#setting"></a></li>
    </ul>

    <div id="edit" class="ed_tab_contents">
      <PrinterDesign :receipt_root="this.receiptRoot" />
    </div>

    <div id="import" title="">
        <p>
            <br />
            <textarea id="import-doc" cols="30" rows="10" placeholder="&lt;epos-print xmlns=&quot;http://www.epson-pos.com/schemas/2011/03/epos-print&quot;&gt;&lt;text lang=&quot;ja&quot; smooth=&quot;true&quot;&gt;&lt;/text&gt;&lt;feed /&gt;&lt;/epos-print&gt;"></textarea><br />
        </p>
        <div id="import-apply" class="ed_btn_primary"></div>
        <p>
            <br />
            <textarea id="import-info" cols="30" rows="8"></textarea><br />
        </p>
    </div>

    <div id="import-confirm" title="">
        <p>?</p>
    </div>

    <div id="edit-confirm" title="">
        <p>?</p>
    </div>

    <div id="edit-error" title="">
        <p id="edit-info"></p>
    </div>

    <div id="preview" class="ed_tab_contents">
        <div id="preview-paper">
            <div id="preview-width">
                <span>512</span><br />
                <img :src="`${this.receiptRoot}/img/v.png`" alt="" /><img :src="`${this.receiptRoot}/img/h.png`" width="510" height="10" alt="" /><img :src="`${this.receiptRoot}/img/v.png`" alt="" /><br />
            </div>
        </div>
        <div class="preview-break"></div>
        <canvas id="preview-rotate" width="576" height="2400"></canvas>        
        <canvas class="preview-line" id="preview-line" width="1" height="1"></canvas>
        <canvas id="preview-text" width="576" height="192"></canvas>
        <canvas id="preview-page" width="576" height="2400"></canvas>
        <canvas id="preview-area" width="576" height="2400"></canvas>
        <br />
        <br />
        <br />
        <br />
        <br />
        <div id="preview-info">
            
        </div>
    </div>

    <div id="print" class="ed_tab_contents">
        <p>
            <br />
            <textarea id="print-doc" cols="60" rows="12" disabled></textarea><br />
        </p>
        <div class="ed_flex ed_flex_center">
          <div id="print-send" class="ed_btn_primary"></div>
          <div id="xml-save" class="ed_btn_primary ed_ml_1rem hidden_menu"><span class="ed_button_span">XML</span></div>
        </div>
    </div>

    <div id="setting" class="ed_tab_contents">
        <h3 class="hidden_menu"></h3>
        <p class="hidden_menu">
             ()<br />
            <select id="setting-model">
                    <option value="tm_t88_80" selected="selected">TM-T88 series (80mm)</option>
                    <option value="tm_t88_58">TM-T88 series (58mm)</option>
            </select>
            <span class="hidden">
                <input id="setting-width" type="text" maxlength="2" size="2" value="80" />mm
            </span>
        </p>
        <br />
        <h3></h3>

        <div class="ed_setting_container">

          <div class="ed_mb_1rem">
            <p class="ed_mb_none"></p>
            <div class="dropdown ed_mx_auto" ref="dropdownRef">
              <div
                class="dropdown-header"
                @click.stop="deviceModalOpen = !deviceModalOpen"
              >
                <span>{{ selectedLabels }}</span>
              </div>
  
              <div v-if="deviceModalOpen" class="dropdown-body">
                <input
                  type="text"
                  v-model="keyword"
                  class="search"
                  placeholder=""
                />
  
                <div v-for="item in filteredItems" :key="item.device_id" class="device_item">
                  <label>
                    <input
                      type="checkbox"
                      v-model="selected"
                      :value="item.device_id"
                    />
                    {{ item.description || item.serial_no }}
                  </label>
                </div>
              </div>
            </div>
          </div>
  
          <div class="ed_mb_1rem">
            <p class="ed_mb_none">ID</p>
            <input id="setting-devid" type="text" v-model="selectedPrinter.printer_id" />
          </div>
          <div class="ed_mb_1rem">
            <p class="ed_mb_none"></p>
            <input id="setting-devname" type="text" v-model="selectedPrinter.printer_name" />
          </div>
          <div class="ed_mb_1rem">
            <p class="ed_mb_none">IP</p>
            <input id="setting-ipaddr" type="text" v-model="selectedPrinter.printer_ip" />
          </div>
          <div class="ed_mb_1rem">
            <p class="ed_mb_none"></p>
            <input id="setting-port" type="text" v-model="selectedPrinter.port" />
          </div>
          <div class="ed_mb_1rem">
            <p class="ed_mb_none"> ()</p>
            <input id="setting-timeout" type="text" v-model="selectedPrinter.print_timeout_ms" />
          </div>
          <br />
          <div class="ed_flex ed_flex_center">
            <div
              id="setting-save"
              class="ed_btn_primary"
              @click="savePrinterSettings"
            >
              <span class="ed_button_span"></span>
            </div>
          </div>

        </div>

    </div>

    <canvas class="hidden" id="epos-image" width="1" height="1"></canvas>

  </div>

  <div id="loadingView">
    <p>...</p>
  </div>

</template>

<script>
const axios = require('axios')
import { loadScript, loadCss } from "@/assets/js/loadResource";
import PrinterDesign from "./ui/PrinterDesign.vue";

const backendUrl = location.origin + '/server/backend'

export default {
  data () {
    return {
      receiptRoot: "/server/user/receipt",
      receiptReady: false,
      defaultPrinterXml: `<epos-print xmlns="http://www.epson-pos.com/schemas/2011/03/epos-print">
</epos-print>`,
      devices: [],
      printers: [],
      deviceModalOpen: false,
      keyword: "",
      selected: [],
      filteredItems: [],
      selectedLabels: "",
      selectedPrinterID: "",
      selectedPrinter: {},
    }
  },
  watch: {
    keyword: function () {
      // 
      this.filteredItems = this.filterDevices(this.keyword);
    },
    selected: function () {
      // 
      this.selectedLabels = this.selectedDeviceLabels(this.selected);
    },
    selectedPrinterID: function () {
      if (!this.receiptReady) return;

      // 
      let self = this
      
      // 
      self.selectedPrinter = self.printers.filter(printer => printer.printer_id === self.selectedPrinterID)[0] || {};
      document.getElementById("print-doc").value  = self.selectedPrinter.print_xml || self.defaultPrinterXml;
      document.getElementById("import-doc").value = self.selectedPrinter.print_xml || self.defaultPrinterXml;
      window.importDoc();
      window.exportDoc();

      // 
      self.selected = [];
      if (!self.selectedPrinterID) return

      axios
        .get(backendUrl + '/printer/linkedDevices/' + self.selectedPrinterID)
        .then(function(response) {
          console.log(response.data)
          self.selected = response.data.map(device => device.device_id)
        })
        .catch(function(e) {
          console.log(e)
          self.selected = []
        })

    },
  },
  components: {
    PrinterDesign,
  },
  async mounted () {

    // CSS
    await loadCss(this.receiptRoot + "/css/smoothness/jquery-ui-1.8.18.custom.css");
    await loadCss(this.receiptRoot + "/css/epos-print-editor.css");

    // JS
    await loadScript(this.receiptRoot + "/js/jquery-1.7.1.min.js");
    await loadScript(this.receiptRoot + "/js/jquery-ui-1.8.18.custom.min.js");
    await loadScript(this.receiptRoot + "/js/jquery.ui.touch-punch.min.js");
    await loadScript(this.receiptRoot + "/js/epos-2.27.0.js");
    await loadScript(this.receiptRoot + "/js/epos-print-editor-ja.js");
    await loadScript(this.receiptRoot + "/js/editor-import.js");
    await loadScript(this.receiptRoot + "/js/editor-export.js");
    await loadScript(this.receiptRoot + "/js/editor-preview.js");
    await loadScript(this.receiptRoot + "/js/editor-print.js");
    await loadScript(this.receiptRoot + "/js/editor-main.js");

    await this.$nextTick();
    this.receiptReady = true;

    // 
    document.getElementById('tabs').classList.remove('ed_hidden_view');
    document.getElementById('loadingView').classList.add('ed_hidden_view');

    // 
    this.initPrinterInfos();
    
    document.addEventListener('click', this.handleClickOutside);

  },
  beforeUnmount() {
    document.removeEventListener('click', this.handleClickOutside)
  },
  methods: {
    initPrinterInfos: function () {
      let self = this
      // facet
      axios
        .get(backendUrl + '/printer/devices/1')
        .then(function(response) {
          console.log(response)
          self.devices = response.data
          self.filteredItems = response.data
        })
        .catch(function(e) {
          console.log(e)
          self.devices = []
          self.filteredItems = []
        })
      // facet
      axios
        .get(backendUrl + '/printer/printers/1')
        .then(function(response) {
          console.log(response)
          self.printers = response.data.sort((a, b) => {
            if (a.id < b.id) return 1
            if (a.id > b.id) return -1
            return 0
          })
        })
        .catch(function(e) {
          console.log(e)
          self.printers = []
        })
    },
    filterDevices: function (keyword) {
      if (!keyword) {
        return this.devices;
      } else {
        return this.devices.filter(i => {
            if (i.description) {
              return i.description.toLowerCase().includes(keyword.toLowerCase())
            } else {
              return i.serial_no.toLowerCase().includes(keyword.toLowerCase())
            }
          }
        );
      }
    },
    savePrinterSettings: function () {
      let self = this

      const printXML = document.getElementById('print-doc').value;
      self.selectedPrinter.print_xml = printXML

      // 
      if (
        !self.selectedPrinter.printer_id ||
        !self.selectedPrinter.printer_name ||
        !self.selectedPrinter.printer_ip ||
        !self.selectedPrinter.port ||
        !self.selectedPrinter.print_timeout_ms
      ) {
        alert('')
        return
      }

      let postParams = {
        printer_id: self.selectedPrinter.printer_id,
        printer_name: self.selectedPrinter.printer_name,
        printer_ip: self.selectedPrinter.printer_ip,
        port: self.selectedPrinter.port,
        print_timeout_ms: self.selectedPrinter.print_timeout_ms,
        print_xml: self.selectedPrinter.print_xml || self.defaultPrinterXml,
        old_printer_id: self.selectedPrinterID,
        device_ids: self.selected,
      }
      if (self.selectedPrinter.id) postParams.id = self.selectedPrinter.id;

      // 
      console.log('printer setting:', postParams)
      axios
        .post(backendUrl + '/printer/setting', postParams)
        .then(function (response) {
          console.log('response data:', response.data)

          // 
          self.initPrinterInfos();
          const printID = (self.selectedPrinter.id) ? postParams.printer_id : response.data.printer_id;
          self.selectedPrinterID = printID;
          self.selectedPrinter = self.printers.filter(printer => printer.printer_id === printID)[0] || {};
          document.getElementById('print-doc').value = self.selectedPrinter.print_xml || self.defaultPrinterXml;
          document.getElementById("import-doc").value = self.selectedPrinter.print_xml || self.defaultPrinterXml;
          window.importDoc();
          window.exportDoc();

          alert('')
        })
        .catch(function (e) {
          console.log(e)
          alert('')
        })

    },
    deletePrinterSettings: function () {
      let self = this

      if (!self.selectedPrinterID) {
        alert('')
        return
      }

      if (!confirm('')) {
        return
      }

      // 
      console.log('delete printer:', self.selectedPrinterID)
      axios
        .post(backendUrl + '/printer/delete', { id: self.selectedPrinter.id })
        .then(function (response) {
          console.log('response data:', response.data)

          // 
          self.selectedPrinterID = ""
          self.selectedPrinter = {}
          document.getElementById('print-doc').value = self.defaultPrinterXml;
          document.getElementById("import-doc").value = self.defaultPrinterXml;
          window.importDoc();
          window.exportDoc();
          self.initPrinterInfos();

          alert('')
        })
        .catch(function (e) {
          console.log(e)
          alert('')
        })

    },
    selectedDeviceLabels: function (selected) {
      if (selected.length === 0) return "";
      return this.devices
        .filter(i => selected.includes(i.device_id))
        .map(i => i.description || i.serial_no)
        .join(", ");
    },
    handleClickOutside(event) {
      if (
        this.$refs.dropdownRef &&
        !this.$refs.dropdownRef.contains(event.target)
      ) {
        this.deviceModalOpen = false
      }
    },
    confirm_back: function () {
      this.$emit('confirm', false)
    },
    exec: function (type) {
      this.$emit('loading', true)

      let self = this

      switch (type) {
        case 'ip':
          axios
          .post(location.origin + '/server/backend/system/ipconfig', self.params)
          .then(function () {
            alert("...");

            // IP
            axios
              .post(location.origin + '/server/backend/system/reboot/manual', "")
              .then(function () {
                console.log("start reboot")
                location.href = 'http://'+self.params.ip.join('.')+'/server/user/'
              })
          })
          .catch(function (e) {
            self.$emit('loading', false)
            alert(e.response.data.msg)
          })
          break;
        case 'ntp':
          axios
            .post(location.origin + '/server/backend/system/ntp', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'ssl':
          axios
            .post(location.origin + '/server/backend/system/ssl', '')
            .then(function (response) {
              console.log(response.data)
              alert('')
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
            .finally(function () {
              location.reload()
            })
          break;
      
        default:
          break;
      }
    },
  }
}
</script>

<style scoped>

.ed_text_start {
  text-align: start;
}
.ed_mt_none {
  margin-top: 0 !important;
}
.ed_mb_none {
  margin-bottom: 0 !important;
}
.ed_ml_none {
  margin-left: 0 !important;
}
.ed_mr_none {
  margin-right: 0 !important;
}
.ed_mt_1rem {
  margin-top: 1rem !important;
}
.ed_mb_1rem {
  margin-bottom: 1rem !important;
}
.ed_ml_1rem {
  margin-left: 1rem !important;
}
.ed_mr_1rem {
  margin-right: 1rem !important;
}
.ed_mx_auto {
  margin-left: auto !important;
  margin-right: auto !important;
}
.ed_flex {
  display: flex !important;
}
.ed_flex_center {
  justify-content: center !important;
  align-items: center !important;
}

.ed_tab_contents {
  border: solid 1px #c0c0c0 !important;
  border-top: none !important;
}

.ed_select_container {
  margin-bottom: 2rem;
}

.ed_hidden_view {
  visibility: hidden;
}

.ed_tabs_container {
  border: none;
  padding: none;
}

.ed_setting_container {
  width: 300px;
  margin: 0 auto;
  text-align: left;
}

#printer-ID {
  width: 300px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg width='12' height='12' ... >");
  background-repeat: no-repeat;
  background-position: right 10px center;
}

.ed_btn_primary {
  padding: 10px 20px !important;
  background: #0078ff !important;
  color: #fff !important;
  border: none !important;
  border-radius: 6px !important;
  font-size: 14px !important;
  font-weight: 600 !important;
  cursor: pointer !important;
  transition: 0.25s ease !important;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15) !important;
}

.ed_btn_primary:hover {
  background: #005fcc !important;
  box-shadow: 0 5px 12px rgba(0, 0, 0, 0.2) !important;
  transform: translateY(-2px) !important;
}

.ed_btn_primary:active {
  background: #004fa8 !important;
  transform: translateY(0px) !important;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15) !important;
}

.ed_button_span {
  padding: .4em 1em;
  display: block;
}

.dropdown {
  position: relative;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.dropdown-header {
  padding: 8px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
}

.dropdown-body {
  border-top: 1px solid #ccc;
  background: #fff;
  padding: 8px;
  position: absolute;
  width: 100%;
  z-index: 10;
  box-shadow: 0 3px 5px gray;
  border-radius: 5px;
}

.search {
  width: 100%;
  padding: 4px;
  margin-bottom: 8px;
  box-sizing: border-box;
}

.device_item {
  margin: 4px 0;
}

.hidden_menu {
  display: none;
}

</style>
