<template>
  <div class="confirm_wrapper">
    <div class="confirm_container">
      <h1></h1>
      <div class="confirm_contents">
        <h2>{{ confirm_list[confim_content] }}</h2>
        <div class="confirm_text" v-html="confirmText(confim_content)">
        </div>
        <div class="confirm_btn_box">
          <div class="btn submit_btn" @click="exec(confim_content)"></div>
          <div class="btn back_btn" @click="confirm_back()"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
const axios = require('axios')
import { saveAs } from 'file-saver'

export default {
  data () {
    return {
      confirm_list: {
        'ip':'IP',
        'ntp':'NTP',
        'sslEnable':'SSL',
        'ssl':'SSL',
        'middleware':'',
        'rebootM':'',
        'rebootA':'',
        'facetUp':'facet',
        'facetLog':'',
        'apiToken':'API',
        'exclusive':''
      },
      week_list: {
        "sun":"",
        "mon":"",
        "tue":"",
        "wed":"",
        "thu":"",
        "fri":"",
        "sat":"",
        "every":""
      }
    }
  },
  props: {
    confim_content: String,
    params: Array
  },
  methods: {
    confirm_back: function () {
      this.$emit('confirm', false)
    },
    exec: function (type) {
      this.$emit('loading', true)

      let self = this

      switch (type) {
        case 'ip':
          axios
          .post(location.origin + '/server/backend/system/ipconfig', self.params)
          .then(function () {
            alert("...");

            // IP
            axios
              .post(location.origin + '/server/backend/system/reboot/manual', "")
              .then(function () {
                console.log("start reboot")
                location.href = 'http://'+self.params.ip.join('.')+'/server/user/'
              })
          })
          .catch(function (e) {
            self.$emit('loading', false)
            alert(e.response.data.msg)
          })
          break;
        case 'ntp':
          axios
            .post(location.origin + '/server/backend/system/ntp', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'ssl':
          axios
            .post(location.origin + '/server/backend/system/ssl', '')
            .then(function (response) {
              console.log(response.data)
              alert('')
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
            .finally(function () {
              location.reload()
            })
          break;
        case 'sslEnable':
          axios
            .post(location.origin + '/server/backend/system/sslSwitch', self.params)
            .then(function (response) {
              console.log(response.data)
              let protocol = ((self.params.ssl_enable === "1") ? 'https' : 'http')
              alert('')
              axios
              .post(location.origin + '/server/backend/system/reboot/manual', "")
              .then(function () {
                location.href = protocol + '://' + location.host + '/server/user/'
              })
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'middleware':
          if (document.facet_admin_form.middleware_file.files[0]) {
            axios
              .post(location.origin + '/server/backend/system/middleware', self.params, {
                headers: {
                  "Content-Type": "multipart/form-data"
                }
              })
              .then(function (response) {
                console.log(response.data)
                location.reload();
              })
              .catch(function (e) {
                self.$emit('loading', false)
                alert(e.response.data.msg)
              })
            
          } else {
            alert("")
          }
          break;
        case 'rebootM':
          axios
            .post(location.origin + '/server/backend/system/reboot/manual', "")
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'rebootA':
          axios
            .post(location.origin + '/server/backend/system/reboot/auto', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              self.$emit('loading', false)
              alert(e.response.data.msg)
            })
          break;
        case 'facetUp':
          if (document.facet_admin_form.facet_file.files[0]) {
            axios
              .post(location.origin + '/server/backend/facet/versionUpdate', self.params, {
                headers: {
                  "Content-Type": "multipart/form-data"
                }
              })
              .then(function (response) {
                console.log(response.data)
                location.reload();
              })
              .catch(function (e) {
                self.$emit('loading', false)
                alert(e.response.data.msg)
              })
            
          } else {
            alert("facet")
          }
          break;
        case 'facetLog':
          axios
            .get(location.origin + '/server/backend/facet/facetLog', {
              responseType: 'blob'
            })
            .then(function (response) {
              let mineType = response.headers['content-type']
              let now = new Date().toLocaleDateString('ja-JP', {year:'numeric',month:'2-digit',day:'2-digit',hour:'2-digit',minute:'2-digit',second:'2-digit'}).replaceAll('/','').replaceAll(':','').replace(' ','')
              const name = 'logs_'+now+'.tar.gz'
              const blob = new Blob([response.data], {type: mineType})
              saveAs(blob, name)

              setTimeout(function () {
                location.reload()
              }, 5000)
            })
            .catch(function (e) {
              alert(e.response.data.msg)
              self.$emit('loading', false)
            })
          break;
        case 'apiToken':
          axios
            .post(location.origin + '/server/backend/facet/apiToken', self.params)
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              alert(e.response.data.msg)
              self.$emit('loading', false)
            })
          break;
        case 'exclusive':
          axios
            .post(location.origin + '/server/backend/facet/exclusiveCancel', '')
            .then(function (response) {
              console.log(response.data)
              alert('')
              location.reload()
            })
            .catch(function (e) {
              alert(e.response.data.msg)
              self.$emit('loading', false)
            })
          break;
      
        default:
          break;
      }
    },
    confirmText: function (type) {
      let self = this
      let text = []

      switch (type) {
        case 'ip':
          if (self.params.ip_type == 'manual') {
            text = [
              '',
              '',
              'IP'+self.params.ip.join('.')+'/'+self.params.subnet,
              'GateWay'+self.params.gw.join('.'),
              'DNS'+self.params.dns.join('.'),
              '',
              '',
              '',
              'facet',
              'IPFaceFC',
              '',
              ''
            ]
          } else {
            text = [
              '',
              '',
              'IPDHCP',
              '',
              'DHCPIP',
              '',
              '',
              'facet',
              '',
              ''
            ]
          }
          break;
        case 'ntp':
          text = [
            '',
            '',
            'NTP'+self.params.ntp,
            ''
          ]
          break;
        case 'ssl':
          text = [
            'SSL',
            '',
            'NTP',
            '',
            'UI',
            'UI'
          ]
          break;
        case 'sslEnable':
          text = [
            'SSL',
            '',
            'SSL'+((self.params.ssl_enable === '1')?"":""),
            '',
            '',
            '',
            '',
            'URLFaceFCURL',
            'facetAPIhttphttps',
            'FaceFCWebSocketwswss',
            '',
            ''
          ]
          break;
        case 'middleware':
          text = [
            '',
            'facetFaceFC',
            '',
            ''
          ]
          break;
        case 'rebootM':
          text = [
            '',
            'facet',
            '',
            ''
          ]
          break;
        case 'rebootA':
          if (self.params.reboot_enable == 'enable') {
            text = [
              '',
              '',
              '',
              ''+self.week_list[self.params.reboot_week],
              ''+self.params.reboot_hour+'',
              '',
              '',
              'facet'
            ]
          } else {
            text = [
              '',
              '',
              '',
              '',
              '',
              ''
            ]

          }
          break;
        case 'facetUp':
          text = [
            'facet',
            '',
            'facet'
          ]
          break;
        case 'facetLog':
          text = [
            '',
            '',
            'facet',
            'facet'
          ]
          break;
        case 'apiToken':
          text = [
            'API',
            'API'
          ]
          break;
        case 'exclusive':
          text = [
            '',
            'facet'
          ]
          break;
        default:
          break;
      }
      return text.join('<br>')
    }
  }
}
</script>

<style scoped>

.confirm_wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 99;
}

.confirm_container {
  width: 60%;
  height: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0 0 10px #000;
  background-color: #fff;
  padding: 3rem;
}

.confirm_contents {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.confirm_btn_box {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 300px;
  margin-top: 2rem;
}

.btn.back_btn {
  background-color: gray;
}

.confirm_text {
  text-align: start;
}

.text_red {
  color: red;
}

.text_bold {
  font-weight: bold;
}

.small {
  font-size: 0.8rem;
}

.large {
  font-size: 1.2rem;
}

</style>
